import React, { Component } from "react";
import { CSSTransition } from 'react-transition-group'
import './style.css'
import Bar from './bar';


class ErrorPage extends Component {
    constructor(props) {
        super(props);
        this.state ={
            messageClasses: ['message'],
        }
    }

    render() {
        return (
            <div id='pageErrorContainer'>
                <CSSTransition in={true} timeout={300} classNames='message' unmountOnExit>
                    <div id='left' className='message'></div>
                </CSSTransition>
            </div>
        );
    }
}

export default ErrorPage;


